        'v': 'ä½ å…·æœ‰è¦–è¦ºå­¸ç¿’çš„å„ªå‹¢ã€‚è¦–è¦ºå„ªå‹¢çš„äººï¼Œé€šå¸¸æ“…æ–¼çœ‹åœ–è¡¨ã€åšåœ–å½¢çš„è¨­è¨ˆèˆ‡è³‡è¨Šæ•´ç†ã€‚<br/>ä½ çš„ç­†è¨˜æœ¬æ‡‰è©²å……æ»¿ä½ è‡ªå·±çš„åœ–é´¨å’Œåœ–è±¡å¼ç­†è¨˜å§ï¼Ÿå¤ªé›œäº‚çš„é¡è‰²èˆ‡ç©ºé–“ï¼Œå¯èƒ½æœƒæ‰“äº‚ä½ çš„æ€è€ƒã€é™ä½å­¸ç¿’æ•ˆç‡ï¼Œæœ€å¥½åœ¨ä¸€å€‹è¦–è¦ºå–®ç´”çš„ç’°å¢ƒä¸‹å­¸ç¿’ã€‚',
        'a': 'ä½ å…·æœ‰è½è¦ºã€å°è©±å­¸ç¿’çš„å„ªå‹¢ã€‚è½è¦ºã€å°è©±å„ªå‹¢çš„äººï¼Œé€šå¸¸ä¹Ÿæ¯”è¼ƒäººéš›å–å‘ï¼Œå–œæ­¡åœ¨è¨è«–çš„éç¨‹ä¸­å­¸ç¿’ï¼Œå–œæ­¡èˆ‡äººåˆä½œï¼Œè€Œä¸æ˜¯æ¯å€‹äººå„åšå„çš„ã€‚<br/>åœ¨ä¸€èˆ¬çš„è¬›è¿°èª²å ‚ï¼Œå–®å‘çš„è½è¬›ä¹Ÿæ˜¯å¯ä»¥å·®å¼·äººæ„åœ°å­¸æœƒæ–°çŸ¥ï¼Œæ›´å–œæ­¡åˆä½œè¨è«–çš„æ°£æ°›ï¼Œä»¥åŠæœ‰ä¾†æœ‰å¾€çš„äº’å‹•ã€‚å¦‚æœä¸èƒ½ç™¼å•å°±æœƒä¸èˆ’æœã€‚å› æ­¤ï¼Œæœ‰å­¸ç¿’å¤¥ä¼´å’Œå¯ä»¥æå•çš„è€å¸«æˆ–è¨è«–å€æ˜¯å¾ˆé‡è¦çš„ã€‚</p>',
        'r': 'ä½ å…·æœ‰é–±è®€å­¸ç¿’çš„å„ªå‹¢ã€‚é–±è®€å­¸ç¿’æ˜¯å°‘æ•¸å®Œå…¨ä¸ä¾è³´çœŸäººäº’å‹•çš„å­¸ç¿’æ–¹å¼ã€‚å°ä½ è€Œè¨€ï¼Œå¥½çš„å­¸ç¿’ææ–™éå¸¸é‡è¦ã€‚å¦‚æœèƒ½æ“…ç”¨åœ–æ›¸é¤¨å’Œç¶²è·¯ï¼Œä½ å°‡èƒ½ä»¥è‡ªå·±çš„æ­¥èª¿å­¸åˆ°å¾ˆå¤šã€‚<br/>å¦‚æœé‡åˆ°ä¸å¥½çš„ææ–™ï¼Œå¯èƒ½æœƒæ¶ˆè€—ä½ çš„å¿ƒåŠ›ï¼Œè®“ä½ äº‹å€åŠŸåŠï¼Œå› æ­¤ï¼Œç•¶åˆå­¸ä¸€é–€é ˜åŸŸæ™‚ï¼Œè‹¥èƒ½æ‰¾åˆ°è¦–é‡åˆæ·±åˆå»£çš„äººï¼Œè«‹ä»–æ¨è–¦çœŸæ­£å¥½çš„å­¸ç¿’ææ–™çµ¦ä½ ï¼Œæœƒæ›´æœ‰æ•ˆç‡ã€‚',
        'k': 'ä½ å…·æœ‰å¯¦ä½œå­¸ç¿’çš„å„ªå‹¢ã€‚å¯¦ä½œå„ªå‹¢çš„äººï¼Œé€šå¸¸éƒ½æœ‰é©šäººçš„å‰µé€ æ½›åŠ›ï¼Œåªæ˜¯åœ¨ä¸€èˆ¬çš„å–®å‘è¬›è¿°èª²å ‚ä¸­ï¼Œé›£ä»¥ç™¼æ®ã€‚<br/>ä½ é©åˆåœ¨å®¶ä¸­å¼„ä¸€å¡Šå°å·¥ä½œå€ï¼Œå‹•æ‰‹æ‹†è£å„ç¨®è£ç½®çš„å°é›¶ä»¶ï¼Œå¯¦ä½œå‡ºä¸€äº›ä½œå“ï¼ŒæŠŠå­¸åˆ°çš„çŸ¥è­˜è£½æˆæ¨¡å‹ã€‚ä½ ä¹Ÿé©åˆåœ¨å¯¦ä½œèª²å ‚ä¸­ï¼Œé€éé‚Šåšé‚Šå­¸çš„æ–¹å¼å­¸ç¿’æ–°çŸ¥ã€‚<br/>ç´™è·Ÿç­†å°ä½ æ˜¯ä¸å¤ ç”¨çš„ï¼Œæœ€å¥½æ‰¾åˆ°å…¶ä»–çš„å­¸ç¿’å·¥å…·ï¼Œä¾‹å¦‚æ£‹å­ã€ç´™ç‰Œã€æ–¹å¡Šã€æ¨¡å‹ã€é›»è…¦ç¨‹å¼ç­‰ç­‰ã€‚'
      }
      minADV = {
        'v': 'ä½ ç›®å‰é‚„ä¸å¤§æ“…é•·è¦–è¦ºå­¸ç¿’ã€‚é‡åˆ°åœ–è±¡èˆ‡åœ–è¡¨æ™‚ï¼Œè©¦è‘—ç”¨ä½ æ“…é•·çš„èƒ½åŠ›ï¼Œä¾‹å¦‚è½è¦ºã€é–±è®€å’Œå¯¦ä½œï¼Œå»åˆ†æå®ƒå€‘å§ï¼',
        'a': 'ä½ ç›®å‰é‚„ä¸å¤§æ“…é•·è½è¦ºèˆ‡å°è©±çš„å­¸ç¿’ã€‚å¦‚æœå¾ˆå¤šçŸ¥è­˜ç”¨è½çš„ä¸æ˜“ç†è§£ï¼Œè©¦è‘—é é–±è®€ã€åœ–è±¡èˆ‡å¯¦ä½œï¼Œä¾†å­¸æœƒå®ƒå§ï¼',
        'r': 'ä½ ç›®å‰é‚„ä¸å¤§æ“…é•·é–±è®€ã€‚å¦‚æœå¾ˆå¤šçŸ¥è­˜ç”¨è®€çš„ä¸æ˜“ç†è§£ï¼Œé‚£éº¼è©¦è‘—æŠŠå®ƒå€‘å”¸å‡ºä¾†ã€æˆ–è½‰æ›æˆåœ–è±¡ã€å‹•ä½œä¾†ç†è§£å§ï¼<br/>å¤§éƒ¨ä»½çš„è‡ªå­¸çŸ¥è­˜éƒ½ä»¥é–±è®€ç‚ºä¸»ï¼Œå› æ­¤ä½ å¯èƒ½éœ€è¦ä¸€äº›æ“…é•·é–±è®€çš„å­¸ç¿’å¤¥ä¼´ï¼Œä»¥åŠé¡˜æ„å›ç­”ä½ å•é¡Œçš„è€å¸«ï¼Œæ‰ä¸æœƒå› ç‚ºé–±è®€ä¸Šçš„å›°é›£è€Œè¢«å¡ä½ã€‚',
        'k': 'ä½ ç›®å‰é‚„ä¸å¤§æ“…é•·å¯¦ä½œå­¸ç¿’ã€‚è©¦è‘—æŠŠä½ å­¸åˆ°çš„çŸ¥è­˜ï¼Œæ‹¿ä¾†å‰µä½œä¸€äº›ä½œå“å§ï¼'
      }
      for (i$ = 0, len$ = vark.length; i$ < len$; ++i$) {
        t = vark[i$]
        res = this.countVARK()
        if (res[t] > 9) {
          ans.push(maxADV[t])
        }
        if (res[t] < 6) {
          ans.push(minADV[t])
        }
      }
      return ans.join('<br/><br/>')
    }
  }
}
</script>

