</template>

<script>

import { chatsRef } from '../firebase'
import mix from '../mixins/mix.js'
import VueMarkdown from 'vue-markdown'

export default {
  name: 'chats',
  mixins: [mix],
  components: { VueMarkdown },
  props: ['id', 'user', 'photoURL'],
  data () {
    return {
      p: '',
      msg: '',
      key: '',
      edit: '',
      read: 0,
      isFull: false,
      isMini: true,
      label: 'é–’èŠ',
      labels: ['è«®è©¢', 'æ•…éšœ', 'æ‰¾ä¼´', 'é–’èŠ']
    }
  },
  firebase: {
    chats: chatsRef
  },
  methods: {
    preview: function () {
      var o = {
        id: this.id,
        n: this.user.providerData[0].displayName,
        t: this.msg,
        l: this.label,
        edit: false,
        photoURL: this.photoURL || '',
        time: (new Date()).getTime()
      }
      this.p = o
    },
    updateChat: function (c) {
      var o = {
        id: c.id,
        n: c.n,
        t: c.t,
        l: c.l,
        photoURL: c.photoURL || '',
        time: (new Date()).getTime()
      }
      chatsRef.child(c['.key']).set(
        o
      )
    },
    addChat: function () {
      var o = {
        id: this.id,
        n: this.user.providerData[0].displayName,
        t: this.msg,
        l: this.label,
        photoURL: this.photoURL || '',
        time: (new Date()).getTime()
      }
      if (this.msg) {
        this.$firebaseRefs.chats.push(o)
        this.msg = ''
      }
    },
    loginFB: function () {
      this.$emit('loginFB')
    },
    loginGoogle: function () {
      this.$emit('loginGoogle')
    },
    fil: function (list) {
      var k = this.key
      return list.filter(function (o) { return (o.t + o.l).indexOf(k) > -1 || !k }).map(function (o) {
        o.edit = false; return o
      })
    },
    reCount: function () {
      this.read = this.chats.length
      this.$localStorage.set('read', this.read)
    }
  },
  mounted () {
    this.read = this.$localStorage.get('read') || 0
  }
}
</script>

